<template>
  <div>
    <table class="list-page" >
      <tr class="list-des">
        <th>
        </th>
        <th>
         标题
         </th>
        <th>
         时间
         </th>
        <th>
          状态
        </th>
        <th>
        操作
         </th>
        <th>
          <button class="new-questionnaire">
            <router-link to="/newbuild">
              + 新建问卷
            </router-link>
          </button>
        </th>
      </tr>
      <tbody class="list-body" >
         <tr v-for="(col,index) in qnPages">
           <td>
             <input type="checkbox" v-bind:value="index"  v-model="checked"/>
           </td>
           <td>
             {{col.title}}
           </td>
           <td>
            {{col.endTime}}
           </td>
           <td>
             {{classMap[col.status]}}
           </td>
           <td>
             <button @click="editPage(index)">
                  编辑
             </button>
             <el-button type="text" @click="deleteQn(index)" class="list-delete" >  删除</el-button>
             <!--<button @click="deleteQn(index)" class="list-delete">-->
                 <!--删除-->
             <!--</button>-->

             <button  @click="qnDetail(index)" class="list-detail">
                 查看数据
             </button>
           </td>

         </tr>
      </tbody>
      <tfoot class="list-foot">
        <tr>
          <td>
            <label ><input type="checkbox"   class="isCheckAll" v-model="AllQnChoose"/>
              全选
            </label>
            <el-button type="text" @click="deleteAll" class="delete-all">删除选中文件</el-button>
          </td>
        </tr>
      </tfoot>
    </table>
  </div>

</template>


<script>
  import {loadAllData,saveToLocal,deleteQuestion} from '../common/js/store.js';

  export default {
    name: 'check',
    data () {
      return {
        //初始化为全不选
        checked: [],
        AllQnChoose: false,
        qnPages: []
      }
    },
    created: function () {
      this.classMap = ['未发布', '发布中', '已结束']
      this.qnPages = loadAllData();
      if (this.qnPages.length === 0 ){
        this.$router.push('newbuild')
      }
    },
    watch: {
      checked: function () {
          if (this.checked.length === this.qnPages.length){
            this.AllQnChoose = true
          }else {
            this.AllQnChoose = false
          }
      },
      AllQnChoose: function() {
        if (this.AllQnChoose){
          //清空数组，不然就多添加index
          this.checked = []
          for (let index of this.qnPages.keys()) {
            this.checked.push(index);
          }
        }
      }

    },
    methods: {
      editPage: function (index) {
        saveToLocal(this.qnPages[index].id)
        this.$router.push('edit')
      },
      qnDetail: function (index) {
        saveToLocal(this.qnPages[index].id)
        this.$router.push('detail')
      },
      deleteQn: function (index) {
        this.$confirm('此操作将永久删除该问卷, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.qnPages.splice(index,1);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });

        //如果没有数据则跳转到新建问卷列表
        if (this.qnPages.length === 0 ){
          this.$router.push('newbuild')
        }
      },
      deleteAll: function () {

        if (this.AllQnChoose){
            //TODO delete
          this.$confirm('此操作将永久删除全部问卷, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {

            //错误的方法：该变量指针指向新的引用，原引用并没有完成删除！！
            //this.qnPages = []

            //正确的方法：
            this.qnPages.length = 0
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.$router.push('newbuild')
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }

      }
    }


  }

</script>


<style lang="scss" scoped>
  @import "../common/style/mixins/flex-center";
  @import "../common/style/mixins/table-th";
  @import "../common/style/mixins/table-td";

  .list-page{

    position: relative;
    margin: 0 auto;
    margin-top: 80px;

    .list-des{
      position: relative;
      width: 100%;
      height: 50px;
      @include table-th(
        $font-size: 16px
      );

      .new-questionnaire{
        position: absolute;
        left: 904px;
        width: 95px;
        height: 25px;
        background-color: #EE7419;
        color: #fff;
        border: 1px solid #EE7419;
        border-radius: 3px;
        a{
          color: #fff;
        }
      }
    }
    .list-body{
      background-color: white;
      text-align: left;
      width: 100%;

      tr{
        display: block;
        padding: 20px;
        transition: background-color .3s cubic-bezier(0,0,0,1);
        border-bottom: 1px solid #EFEFEF;
        &:hover{
          background-color: #EE7419;
        }
        &:nth-child(1){
          padding-top: 40px;
        }
      }

      @include table-td(
        $font-size: 16
      )

      .list-delete{
        span{
          color: black;
        }
      }


    }
    .list-foot{
      text-align: left;
      background-color: white;
      width: 100%;

      @include my-button($btn-font-size: 16);

      tr{
        height: 80px;
        font-size: 16px;

        td{
          padding-left: 40px;

          .isCheckAll{
            padding-left: 40px;
          }
          .delete-all{
            width: auto;
            border: 1px solid #777;
            border-radius: 2px;
            background-color: #fff;
            padding-top: 3px;
            padding-bottom: 3px;
            padding: 10px;
            font-family: inherit;
            margin-left: 20px;
            color: black;


          }

        }
      }




    }
  }

  h1{
    background-color: red;
  }
</style>



